//================================Primary style=================================
body {
    // min-width: 320px;
    // overflow-x: hidden;

    font-family: $fontFamily, Arial, Helvetica, sans-serif;
    font-weight: 400;
    font-size: 16px;
    line-height: 18px;
    color: $text-color;

    background-color: linear-gradient(180deg, #F8F8F8 -45.04%, rgba(248, 248, 248, 0) 88.56%);
}

body.lock {
    overflow: hidden;
}

button {
    cursor: pointer;
}

a {
    display: block;
}

footer {
    flex-shrink: 0;
}
//================================Primary style=================================

//================================Modules=======================================
.title {
    font-weight: 500;
    @include font-size(35px);
    line-height: 1.5;
    color: $text-color-bold;
}

.subtitle {
    font-weight: 400;
    font-size: 16px;
}

.main-logo {
    display: flex;
    align-items: center;

    &__img {
        margin: 0px 10px 0px 0px;
    }

    &__text {
        font-size: 20px;
        font-weight: 500;
        color: $text-color-bold;
    }
}

.main-btn {
    position: relative;

    padding: 18px 77px;
    color: #fff;
    font-weight: 700;
    background-color: $accent;
    border-radius: 10px;

    &::before {
        display: block;
        margin: 0 auto;

        content: "";
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        width: 85%;
        height: 100%;
        opacity: 0.35;
        border-radius: 10px;
        box-shadow: 0 25px 54px $accent;
    }
}

.social-item {
    margin: 0px 20px 0px 0px;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background-color: #fff;

    box-shadow: 0px 13px 18px rgba(17, 19, 35, 0.08);

    display: flex;
    justify-content: center;

    &:last-child {
        margin: 0;
    }

    &:hover {
        background-color: $accent;
    }
}

.social-link {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 17px;
    color: $accent;
    width: 100%;

    &:hover {
        color: #fff;
    }
}

//================================Modules=======================================

//=================================Wrapper=======================================
.wrapper {
    position: relative;
    overflow-x: hidden;
    min-height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.page {
    flex: 1 0 auto;
}
//=================================Wrapper=======================================

// [Grid layout]
.container {
    max-width: 1170px;
    padding: 0 15px;    
    margin: 0 auto;
}
// [Grid layout]