.sign-in-form {
    text-align: center;

    &__title {
        margin: 0px 0px 20px 0px;
        font-size: 30px;
        color: $text-color-bold;
        font-weight: 700;
    }

    &__social {
        margin: 0px 0px 20px 0px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__text {
        margin: 0px 0px 20px 0px;
    }

    &__field {
        margin: 0px 0px 20px 0px;
    }

    &__input {
        background-color: #eee;
        width: 100%;
        padding: 12px 15px;
        margin: 0px 0px 10px 0px;
        border-radius: 20px;

        &:last-child {
            margin: 0;
        }
    }

    &__forgot-password-link {
        margin: 0px 0px 20px 0px;
        color: inherit;
    }

    &__btn {
        border-radius: 50px;
        font-size: 16px;

        &::before {
            box-shadow: none;
        }

        &:hover {
            background-color: #ce2e46;
        }
    }
}

.sign-in-modal__window.hystmodal__window, 
.subscribe-modal__window.hystmodal__window {
        padding: 30px;
        border-radius: 10px;
    }

.modal__close.hystmodal__close {
    position: absolute;
        top: 15px;
        right: 15px;
        width: 40px;
        height: 40px;
        border-radius: 50%;
        background-color: $accent;

        background-image: none;
}
.modal__close-icon {
    display: inline-block;
        position: absolute;
        left: 50%;
        top: 50%;
        bottom: auto;
        right: auto;
        transform: translateX(-50%) translateY(-50%);
        width: 18px;
        height: 2px;
        background-color: rgba(#fff, 0);

        border-radius: 1px;

        &::before, &::after {
            content: "";
            position: absolute;
            width: 100%;
            height: 100%;
            left: 0;

            border-radius: 1px;
            background-color: inherit;

            background-color: rgba(#fff, 1);
        }

        &:before {
            bottom: 0;
            transform: rotate(45deg);
        }

        &:after {
            top: 0;
            transform: rotate(-45deg);
        }
}

.subscribe-modal__close.hystmodal__close {
    right: 10px;
    top: -16px;
}

