.subscribe {
    z-index: 2;
    position: absolute;
    bottom: 8.7%;
    width: 100%;

    @media (min-width:$md1+px){
        bottom: 12%;
    }

    @media (min-width:$md2+px){
        bottom: 10%;
    }

    @media (min-width:$md4+px){
        bottom: 9%;
    }

    &__inner {
        position: relative;
        border-radius: 10px;
        background-color: #fff;

        @include padding(20px);
        // padding: 20px;

        text-align: center;
        justify-content: center;
        flex-direction: column;
        align-items: center;

        @media (min-width:$md2+px){
            padding: 59px 10px;
            text-align: left;
            display: flex;
            flex-direction: row;
            justify-content: space-around;
        }

        @media (min-width:$md1+px){
            
        }

        &::before {
            border-radius: 10px;
            box-shadow: 0 53px 114px #0d1025;
            content: "";
            height: 100%;
            left: 0;
            margin: 0 auto;
            opacity: .06;
            position: absolute;
            right: 0;
            top: 0;
            width: 100%;
        }
    }

    &__info {
    }

    &__title {
        margin: 0px 0px 20px 0px;
    }

    &__subtitle {
        @media (min-width:$md1+px){
            margin: 0px 0px 20px 0px;
        }
        @media (min-width:$md3+px){
            margin: 0;
        }
    }
}
