@media (max-width: 992px) {
  .nav-toggle {
    z-index: 10;
    position: relative;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    border: 2px solid #fff;
    background-color: #F53855;
    cursor: pointer;
  }
  .nav-toggle__icon {
    display: inline-block;
    position: absolute;
    left: 50%;
    top: 50%;
    bottom: auto;
    right: auto;
    transform: translateX(-50%) translateY(-50%);
    width: 18px;
    height: 2px;
    background-color: #fff;
    border-radius: 1px;
  }
  .nav-toggle__icon::before, .nav-toggle__icon::after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    border-radius: 1px;
    background-color: inherit;
  }
  .nav-toggle__icon::before {
    bottom: 5px;
  }
  .nav-toggle__icon::after {
    top: 5px;
  }
  .nav-toggle.active .nav-toggle__icon {
    background-color: rgba(255, 255, 255, 0);
  }
  .nav-toggle.active .nav-toggle__icon:before, .nav-toggle.active .nav-toggle__icon:after {
    background-color: white;
  }
  .nav-toggle.active .nav-toggle__icon:before {
    bottom: 0;
    transform: rotate(45deg);
  }
  .nav-toggle.active .nav-toggle__icon:after {
    top: 0;
    transform: rotate(-45deg);
  }
}

