  top: 50%;
  bottom: auto;
  right: auto;
  transform: translateX(-50%) translateY(-50%);
  width: 18px;
  height: 2px;
  background-color: rgba(255, 255, 255, 0);
  border-radius: 1px;
}

.modal__close-icon::before, .modal__close-icon::after {
