.footer {
    position: relative;
    padding: 180px 0px 100px 0px;

    background-color:  #F8F8F8;

    &__inner {
        display: flex;
        justify-content: space-between;

        @media (max-width:$md4+px){
            flex-direction: column;
            justify-content: center;
        }
    }

    &__info {
        @media (max-width:$md4+px){
            margin: 0px 0px 30px 0px;
        }
    }

    &__logo {
        margin: 0px 0px 20px 0px;
    }

    &__desc {
        margin: 0px 0px 30px 0px;
        max-width: 340px;
        line-height: 2;
    }

    &__social {
        margin: 0px 0px 30px 0px;
        display: flex;
    }

    &__copyright {
        font-size: 16px;
        color: #AFB5C0;

        @media (max-width:$md4+px){
            position: absolute;
            bottom: 20px;
        }
    }

    &__menu {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-row-gap: 30px;

        @media (min-width:$md2+px){
            grid-column-gap: 100px;
            grid-template-columns: 1fr 1fr 1fr;
            grid-template-rows: 1fr;
        }
    }

    &__item-header {
        margin: 0px 0px 35px 0px;
        font-size: 18px;
        font-weight: 500;
        color: $text-color-bold;
    }

    &__item-list {
        font-size: 16px;
        font-weight: 400;
    }

    &__list-item {
        margin: 0px 0px 25px 0px;

        &:last-child {
            margin: 0;
        }
    }

    &__list-link {
        position: relative;
        display: inline-block;
        color: inherit;

        &:hover {
            &::after {
                content: "";
                width: 100%;
                height: 2px;
                border-radius: 2px;
                background-color: $accent;

                position: absolute;
                top: 110%;
                left: 0;
                right: 0;
            }
        }
    }
}
